<template>
  <div >
    <form method="post" target="upload" action="" encType="multipart/form-data">
    <el-row :gutter="5" >


      <el-col :span="20">

        <el-col :span="3" name="imgType" >

          <el-button id="imgType-1" style="display: block; width: 130px" @click="chooseImgType('imgType-1')"></el-button>
          <el-button id="imgType-2" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-2')">EBC</el-button>
          <el-button id="imgType-3" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-3')"></el-button>
          <el-button id="imgType-4" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-4')" ></el-button>
          <el-button id="imgType-5" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-5')"></el-button>
          <el-button id="imgType-6" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-6')"></el-button>
          <el-button id="imgType-7" style="display: block;margin-left: 0px;width: 130px" @click="chooseImgType('imgType-7')" ></el-button>

        </el-col>


        <el-col :span="16" name="imgList" >
          <div  class="el-upload__tip"><b>,,!</b></div>
          <!--  multiple :limit="5"-->
          <el-upload
            class="upload-demo"
            ref="upload"
            name="files"
            multiple
            :limit="10"
            :on-exceed="uploadExceed"
            :before-upload="beforeUploadPicture"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :on-success="onSuccess"
            :on-error="uploadError"
            :action="action"
            :auto-upload="false"
            :file-list="fileList"
            :data="params"
            list-type="picture-card" style="height: auto">
            <i class="el-icon-plus"></i>

          </el-upload>

          <el-dialog :visible.sync="dialogVisible">

            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>

        </el-col>
      </el-col>

      <el-col :span="4" name="option" style="padding: 230px 0px 0px 0px">
        <!--<el-button size="" type="primary"></el-button>-->
        <el-button size="small" type="primary" @click="submitUpload"></el-button>
        <el-button size="small" @click="download"></el-button>
        <el-button size="small" type="danger" @click="deleteDialog = true"></el-button>
      </el-col>
    </el-row>
    </form>



    <el-dialog
      title=""
      :visible.sync="deleteDialog"
      width="80%">
      <el-upload
        name="file"
        multiple
        :limit="10"
        :on-exceed="uploadExceed"

        :on-remove="handleRemove"
        :on-success="onSuccess"
        :action="action"
        :auto-upload="false"
        :file-list="delShowImgList"
        list-type="picture-card" style="height: auto">
      </el-upload>

     <!-- <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>-->

      <span slot="footer" class="dialog-footer">
    <el-button @click="delImgs(false)"> </el-button>
    <el-button type="primary" @click="delImgs(true)"></el-button>
      </span>
    </el-dialog>

  </div>




</template>

<script>


    export default {
        name: "images",
      data(){
          return{
            multiple:true,
            fileType:'IMAGE',
            deleteDialog:false, //
            delImgList:[],      //id
            delShowImgList:[],  //
            imgTypeId:1,        //
            dialogImageUrl: '', // 
            dialogVisible:false,
            params:{
                    productId:0,
                    typeId:1, //
            },
            imageUrl: '',
            action:this.URL_ROOT + this.URL_PREFIX + "/productFile/saveFiles",
            uploadComplete: true, // 
            imgVisible: false, // 

            photo: '',  // 
            fileList: []
          }
      },

      methods:{
        init(){
          console.log("img init method")
          //
          this.buttonChecked("imgType-1")
          this.searchImg(this.$store.state.product.productId,this.imgTypeId)

        },
        searchImg(productId,typeId){

          //
          this.fileList = []
          this.delShowImgList = []
          this.delImgList = []


          if(productId == null || productId == ''){
            this.$message("")
            return false
          }

          var data = {
            'productId':productId,
            'typeId':typeId,
            'fileType':this.fileType,
          }
          console.log(data.fileType)

          var url = this.URL_ROOT + this.URL_PREFIX + "/productFile/findByProductIdAndTypeId"
          this.$ajax({
            method:'get',
            url:url,
            params:data,

          }).then(res => {
            if(res.data.code == "200"){
              //
              // this.fileList = res.data.data
              this.fileList = res.data.data
            //  $(".upload-demo ul").show()

            }
          }).catch(error =>{
            console.error(error)
          })
        },
        submitUpload() {

          this.$refs.upload.submit();

        },
        // 
        handleRemove(file, fileList){
          console.log("")
          console.log(file);
          // list
          this.delImgList.push(file.productImgId)
          var data = {url : file.url,id:file.productImgId}
          this.delShowImgList.push(data)

         console.log(this.delImgList)
         console.log(this.delShowImgList)

          // this.fileChange(fileList);
        },
        // 
        beforeUploadPicture(file) {
          console.log("")
          console.log(file)
          this.params.productId = this.$store.state.product.productId
        },
        // 
        uploadError(err, file, fileList) {
          if (err) {
            console.log(err.stack);
          }
          this.$message.error(",");
        },
        // 
      onSuccess:function(res,file,fileList){
          console.log("")
          console.log(fileList)

        if(res.code == "200"){
          var imgList = res.data
          console.log(imgList)
          this.fileList = imgList

          console.log(this.fileList)

        }
        this.uploadComplete = true;

       // this.fileChange(fileList)
        },

        // 
        handlePictureCardPreview(file) {
          console.log("")
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;


        },
        uploadExceed(file,fileList){
          console.log("uploadExceed")
          this.$message.warning(` 10  ${file.length}  ${file.length + fileList.length} `);

        },
        chooseImgType(id){
          console.log(id)
          //id 
          var typeId = parseInt(id.toString().split("-")[1])

          console.log(typeId)

          //
         // this.$refs.upload.clearFiles()
          // 
          this.buttonChecked(id)

          //
          this.params.typeId = typeId
          this.imgTypeId = typeId

          this.searchImg(this.$store.state.product.productId,typeId)
        },
        buttonChecked(id){
          $("#"+id).css("background-color","#409efb").css("color","#FFF")
          $("#"+id).siblings().css("background-color","#FFF").css("color","#606266")

        },
        delImgs(flag){

          console.log(this.delImgList)
          if(this.delImgList == null || this.delImgList.length == 0){
            this.$message.warning("")
            this.deleteDialog = false
            return false;
          }

          this.deleteDialog = true

          console.log(this.delShowImgList.toString())
          if(flag){ //
            var url = this.URL_ROOT + this.URL_PREFIX + "/productFile/dropFiles"
            var data = {
              fileIds:this.delImgList.toString(),
              fileType:this.fileType,
            }

            console.log("  ")
            console.log(data.fileType)
            this.$ajax({
              method:'post',
              url:url,
              data: this.qs.stringify(data),

            }).then(res => {
              if(res.data.code == "200"){
                console.log(res.data.data)
                // 
                this.fileList = res.data.data
                this.$message.success("")
              }
            }).catch(error => {
              console.log(error)
            })


          }else{  //
            this.searchImg(this.$store.state.product.productId,this.imgTypeId)
          }
          //list
          this.delImgList = []
          this.delShowImgList = []

          this.deleteDialog = false

        },
        download(){
          //
          console.log(this.$refs.upload.uploadFiles)


          this.$confirm(',', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning'
         }).then(() => {
           this.downloadIamge();
         });

        },
        downloadIamge(){

          var uploadFiles = this.$refs.upload.uploadFiles
          // var ids = new Array(uploadFiles.length)
          var ids = []
          for(var index in uploadFiles){
            var id = uploadFiles[index].productImgId;
            // ids[index] = id
              ids.push(id)
          }

          console.log(ids)

          var url = this.URL_ROOT + this.URL_PREFIX + "/productFileDownload/zipDownload"
          this.$ajax.get(url,{
              params:{
                ids:ids.toString(),
                type:this.fileType
              }
            }

          ).then(res => {

            console.log(res)
            console.log(res.request.responseURL)

            if(res.status == 200){

              //
              open(res.request.responseURL)

              this.$message(res.data.data)

            }
          }).catch(error => {
            this.$message.error(error)
          })

        }

      }


    }
</script>

<style scoped>

  /*.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }*/

</style>



