<template>
    <div id="Principal">
      <el-button style="float: left" @click="addPrincipalMethod"></el-button>
      <el-dialog title="/"  :visible.sync="addPrincipal" style="width: auto; height: auto" @closed="closeDialog">
        <el-form :model="form" label-width="90px" :label-position="labelPosition">

          <el-row >
            <el-col :span="6">
              <el-form-item label=":">
                <span>{{this.$store.state.product.productModelNumber}}</span>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label=":">
                <span>{{form.deptName}}</span>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row >
            <el-col :span="6">
              <el-form-item label="BU:">
                <span>{{form.BU}}</span>
                <input hidden type="number" v-model="form.BuId"/>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":">
                <span>{{form.deptUser}}</span>
              </el-form-item>
              <input hidden type="number" v-model="form.deptUserId"/>
            </el-col>
          </el-row>


          <el-row >
            <el-col :span="6">
              <el-form-item label=":">
                <span>{{form.productCategory}}</span>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label=":">
                <el-select  placeholder="" v-model="form.country" @change="chooseCountry">
                  <el-option v-for="country in countryList" :label="country.countryName" :key="country.countryId" :value="country.countryId"></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label="ASIN:">
                <el-select  placeholder="ASIN" v-model="form.asin" allow-create filterable>
                  <el-option v-for="asin in asinList" :key="asin" :value="asin" :label="asin"></el-option>
                </el-select>
              </el-form-item>
            </el-col>

          </el-row>


          <el-row>
            <el-col :span="18">
              <el-form-item label=":">
                <el-input type="textarea" v-model="form.remark" :autosize="{minRows:3,maxRows:6}"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="addPrincipal = false"> </el-button>
          <el-button type="primary" @click="submitPrincipal"> </el-button>
        </div>

      </el-dialog>




      <el-table name="showTable" border="border" height="250px" style="width: 100%" :data="principalList" @cell-click="editPrincipal">
        <el-table-column prop="countryName" label="" ></el-table-column>
        <el-table-column prop="productModelNumber" label="" ></el-table-column>
        <el-table-column prop="asin" label="ASIN" >

        </el-table-column>
        <el-table-column prop="productCategory" label="" ></el-table-column>
        <el-table-column prop="businessUnit" label="BU" ></el-table-column>
        <el-table-column prop="departmentName" label="" ></el-table-column>
        <el-table-column prop="userName" label="" ></el-table-column>
        <el-table-column prop="record" label="" ></el-table-column>
        <el-table-column prop="uTime" label="" ></el-table-column>
        <el-table-column prop="remark" label="" ></el-table-column>
        <el-table-column  label="" ><el-button @click="addPrincipal = true"></el-button></el-table-column>
      </el-table>
    </div>
</template>

<script>
    export default {
        name: "Principal",
      data(){
          return{
            labelPosition:'right',
            addPrincipal:false,
            principalList:[],
              form:{
                deptName:'',
                BU:'',
                BuId:0,
                deptUser:'',
                productCategory:'',
                country:'',
                oldCountryId:0,
                asin:'',
                deptUserId:0,
                remark:'',
              },
            countryList:[],
            asinList:[],
          }
      },

      methods:{
          init(){
            this.searchPrincipal(this.$store.state.product.productId)
          },
        searchPrincipal(productId){

          if(productId == null || productId == ''){
            this.$message("")
            return false
          }

            var url = this.URL_ROOT + this.URL_PREFIX +"/findProductCharge/findProductUserChargeByProductId/" + productId
            this.$ajax.get(url,{

            }).then( res => {
              if(res.data.code == "200"){
                console.log(res.data.data)
                this.principalList = res.data.data
                if(this.principalList == null || this.principalList.length == 0){
                  this.$message("")
                }
              }
            }).catch(error => {
              console.log(error)
            })

        },
        submitPrincipal(){
          this.addPrincipal = false
          console.log("")
          var url = this.URL_ROOT + this.URL_PREFIX
          if(this.form.oldCountryId == 0){  //
            url += "/findProductCharge/addProductUserCharger"
          }else{  //
            url += "/findProductCharge/updateProductUserCharger"
          }

          var product = this.$store.state.product
          var data = {
            "asin": this.form.asin,
            "countryId": this.form.country,
            "oldCountryId": this.form.oldCountryId,
            "productId": product.productId,
            "record": "",
            "remark": this.form.remark,
            "userId": 2,
          }

          console.log(data)

          this.$ajax({
            method:'post',
            url:url,
            data:data,

          }).then(res => {
            if(res.data.code == '200'){

              console.log("------")

              var msg = res.data.data
              console.log(msg)

              if(this.form.oldCountryId == 0){
                this.$message( msg)
              }else{
                this.$message("" + msg)
              }

              var type = typeof (msg)
              if(res.data.data != null &&  type != "string" ){
                this.principalList = res.data.data
              }

            }
          }).catch(error => {
            this.$message.error(error)
          })

        },
        chooseCountry(){

            this.asinList = []
            this.form.asin = ""
            this.form.remark = ""

          console.log("chooseCountry")
          // console.log(this.form.oldCountryId)

          if(this.form.oldCountryId != 0){  // 
            this.form.oldCountryId = this.form.country
          }
          // console.log(this.form.oldCountryId)

          this.findProductAsinByProductIdAndCountryId(this.$store.state.product.productId,this.form.country);
        },
        findCountryByProductId(){
          var url = this.URL_ROOT + this.URL_PREFIX + "/findProductCharge/findCountry"

          this.$ajax.get(url,{

          }).then(res => {
            if(res.data.code == '200'){
              console.log(res.data.data)
              this.countryList = res.data.data

            }
          }).catch(error => {
            console.log(error)
          })

        },
        findProductAsinByProductIdAndCountryId(productId,countryId){

            var url = this.URL_ROOT + this.URL_PREFIX + "/findProductCharge/findProductAsinByProductIdAndCountryId"
            var data ={
              countryId:countryId,
              productId:productId,}
            this.$ajax({
              method: 'post',
              url:  url,
             data:  this.qs.stringify(data), //

              }).then(res => {
              if(res.data.code == "200"){
                console.log("==findProductAsinByProductIdAndCountryId==")
                console.log(res.data)
                this.asinList = res.data.data
              }
            }).catch(error => {
              console.log(error)
            })
          },
        addPrincipalMethod(){
            this.form.country = ""
            this.form.asin = ""
            this.addOrEdit("add",null)

        },
        editPrincipal(row,cell,column,event){    // row cellnull
            console.log("editPrincipal  ====" )
            console.log(cell)
            console.log(row)
            console.log(column)
            console.log(event)
          //row.asin = 123
         //   console.log(row.getProperty(cell.property))
       //   row.setAttribute(cell.property,"aaa")
            if(cell.label != ""){
              return false
            }
          this.addOrEdit('edit',row)

        },
        addOrEdit(type,row){
          console.log(type)
          var principalList =  this.principalList[0]
          console.log("---principalList--")
          console.log(principalList)

          if(principalList == null){
            this.$message("")
            return false
          }

          //

          this.addPrincipal = true
          var product = this.$store.state.product;
          this.form.productCategory = product.productCategory
          this.form.BU = principalList.businessUnit
          this.form.BuId = principalList.businessUnitId
          this.form.deptName = principalList.departmentName
          this.form.deptUser = principalList.userName
          this.form.deptUserId = principalList.userId
          // 
          this.findCountryByProductId()
            if(type == 'edit'){
              this.form.country = row.countryId
              this.form.oldCountryId = row.countryId
              this.form.asin = row.asin
              this.remark = row.remark
              //asin
              //  this.findProductAsinByProductIdAndCountryId(this.$store.state.product.productId,this.form.country);
            }

        },
        closeDialog(){
          console.log("closeDialog ")
          this.form = {
            deptName:'',
            BU:'',
            BuId:0,
            deptUser:'',
            productCategory:'',
            country:'',
            oldCountryId:0,
            asin:'',
            deptUserId:0,
            remark:'',
          }
        }



      }
    }
</script>

<style scoped>

</style>



