<template>
    <div id="Document">
      <el-row :gutter="5" >

        <el-col :span="20">

          <el-col :span="4" name="docType" >

            <el-button id="docType-1" style="display: block; width: 130px" @click="chooseDocType('docType-1')">listing</el-button>
            <el-button id="docType-2" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-2')"></el-button>
            <el-button id="docType-3" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-3')"></el-button>
            <el-button id="docType-4" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-4')"></el-button>
            <el-button id="docType-5" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-5')"></el-button>
            <el-button id="docType-6" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-6')"></el-button>
            <el-button id="docType-7" style="display: block;margin-left: 0px;width: 130px" @click="chooseDocType('docType-7')"></el-button>

          </el-col>

          <el-col :span="16" name="docList">
            <el-upload
              ref="upload"
              name="files"
              class="upload-demo"
              multiple :limit="10"
              :on-exceed="uploadExceed"
              :action="action"
              :before-upload="beforeUploadPicture"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="onSuccess"
              :on-error="uploadError"
              :file-list="fileList"
              :auto-upload="false"
              :data="params"
              style="height: auto">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip"><b>,,!</b></div>
            </el-upload>

            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>

          </el-col>
        </el-col>

        <el-col :span="4" name="option" style="padding: 230px 0px 0px 0px">
          <el-button size="small" type="primary" @click="submitUpload"></el-button>
          <el-button size="small" @click="download"></el-button>
          <el-button size="small" type="danger" @click="deleteDialog = true"></el-button>
        </el-col>

      </el-row>



      <el-dialog
        title=""
        :visible.sync="deleteDialog"
        width="80%">
        <el-upload
          :action="action"
          :auto-upload="false"
          :file-list="delShowDocList"
          style="height: auto">
        </el-upload>

        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>

        <span slot="footer" class="dialog-footer">
    <el-button @click="delDocs(false)"> </el-button>
    <el-button type="primary" @click="delDocs(true)"></el-button>
      </span>
      </el-dialog>

    </div>
</template>

<script>

    export default {
        name: "Document",
      data(){
          return{
            deleteDialog:false,
            dialogVisible:false,
            uploadComplete:false, //
            dialogImageUrl:'',
            params:{
              productId:0,
              typeId:1, //
            },

            fileType:'DOCUMENT',
            action:this.HOST + "/productFile/saveFiles",
            docTypeId:1,
            delShowDocList:[], // 
            delDocList:[], //id
            fileList: [],

          }
      },
      methods:{
          init(){
            console.log("doc init method")

            this.buttonChecked('docType-1')
            this.searchDocument(this.$store.state.product.productId,this.docTypeId)
          },
        searchDocument(productId,typeId){

          if(productId == null || productId == ''){
            this.$message("")
            return false
          }

          //
          this.fileList = []
          this.delShowDocList = []
          this.delDocList = []

          var data = {
            productId:productId,
            typeId:typeId,
            fileType:this.fileType,
          }

          var url = this.URL_ROOT + this.URL_PREFIX + "/productFile/findByProductIdAndTypeId"
          this.$ajax({
            method:'get',
            url:url,
            params:data,

          }).then(res => {
            if(res.data.code == "200"){
              //
              console.log(res.data)
              this.fileList = res.data.data

            }
          }).catch(error =>{
            console.error(error)
          })

        },
        uploadExceed(file,fileList){
          console.log("uploadExceed")
          this.$message.warning(` 10  ${file.length}  ${file.length + fileList.length} `);

        },
        handleRemove(file, fileList){
          console.log("")
          console.log(file);
          this.delDocList.push(file.productDocumentId)
          var data = {url : file.url,name:file.name,id:file.productDocumentId}
          this.delShowDocList.push(data)

          console.log(this.delDocList)
          console.log(this.delShowDocList)
        },
        handlePreview(file){
          console.log("")
          console.log(file.url);
          console.log(file.name);
         /* this.dialogImageUrl = file.url;
          this.dialogVisible = true;*/
        },
        uploadError(err, file, fileList) {
          this.$message.error("," + err);
        },
        onSuccess:function(res,file,fileList){
          console.log("")
          console.log(res.data)
          if(res.code == "200"){

            this.$message(res.msg)
            this.fileList = res.data

          }
          this.uploadComplete = true;
          // this.fileChange(fileList)
        },
        submitUpload() {
          console.log("")
          this.$refs.upload.submit();
        },
        beforeUploadPicture(file) {
          console.log("")
          console.log(file)
          this.params.productId = this.$store.state.product.productId
        },
        chooseDocType(id){
            console.log(id)
            this.buttonChecked(id)

            //id 
            var typeId = parseInt(id.toString().split("-")[1])
            console.log(typeId)

            //
            this.params.typeId = typeId
            this.docTypeId = typeId

          this.searchDocument(this.$store.state.product.productId,this.docTypeId)
        },
        buttonChecked(id){
          $("#"+id).css("background-color","#409efb").css("color","#FFF")
          $("#"+id).siblings().css("background-color","#FFF").css("color","#606266")

        },
        delDocs(flag){

          if(this.delDocList == null || this.delDocList.length == 0){
            this.$message.warning("")
            this.deleteDialog = false
            return false;
          }

          this.deleteDialog = true

          console.log(this.delShowDocList.toString())
          console.log(this.delDocList.toString())
          if(flag){ //
            var url = this.URL_ROOT + this.URL_PREFIX + "/productFile/dropFiles"
            var data = {
              fileIds:this.delDocList.toString(),
              fileType:this.fileType,
            }

            console.log("  ")
            console.log(data.fileType)
            this.$ajax({
              method:'post',
              url:url,
              data: this.qs.stringify(data),

            }).then(res => {
              if(res.data.code == "200"){
                console.log(res.data.data)
                // 
                this.fileList = res.data.data
                this.$message.success("")
              }
            }).catch(error => {
              console.log(error)
            })


          }else{  //
            this.searchDocument(this.$store.state.product.productId,this.imgTypeId)
          }
          //list
          this.delDocList = []
          this.delShowDocList = []

          this.deleteDialog = false

        },
        download(){
          console.log("download")
          //
          console.log(this.$refs.upload.uploadFiles)

          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.downloadDocment()
          });

        },
        downloadDocment(){
        var ids = []
        var uploadFiles = this.$refs.upload.uploadFiles

        for(var index in uploadFiles){
          var id = uploadFiles[index].productDocumentId;
          ids.push(id)
        }

        console.log(ids)

        var url = this.URL_ROOT + this.URL_PREFIX + "/productFileDownload/zipDownload"
        this.$ajax.get(url,{
            params:{
              'ids':ids.toString(),
              'type':this.fileType
            }
          }

        ).then(res => {
          if(res.status == 200){
            //
            open(res.request.responseURL)

            this.$message(res.data.data)
          }
        }).catch(error => {
          this.$message.error(error)
        })

      }

      }
    }
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



